import React, { useState } from 'react' 
// import copy from "copy-to-clipboard";
// import PropTypes from 'prop-types'

export default function Textform(props){

  const [text, setText] = useState("");
   const deleteAll =()=>{
     setText("")
     props.showAlert('delet all text' ,'danger')
   }

  const copyToClipboard = () => {
    let text = document.getElementById("myInput");
    text.select();
    navigator.clipboard.writeText(text.value);
    document.getSelection().removeAllRanges();
    props.showAlert('copied all text' ,'success')
  };


  const handleExtraSpaces = () => {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.showAlert('remove ectra spaces' ,'warning')
  };

  
  const handelUpCLick = () => {
    console.log("the handelupclickedwas clicked" + text);
    let newtext = text.toUpperCase();
    setText(newtext);
    props.showAlert('convert to uppercase' ,'primary')
  };
  const handelLowCLick = () => {
    console.log("the handelupclickedwas clicked" + text);
    let newtext = text.toLocaleLowerCase();
    setText(newtext);
    props.showAlert('convert to lowercase' ,'primary')
  };
  const handelOnChange = (event) => {
    console.log("the handelonchanced clicked");
    setText(event.target.value);
  };
  // thie funtion never stops just loops
  // setText("this is the text")

  // return----------------------------------------------------
  return (
    <div>
      <div className="container mb-3" style={{ color:props.mode=== 'dark'?'white': 'black'}}>
        <h1>{props.heding}</h1>
        <textarea
          className="form-control"
          id="myInput"
          value={text}
          onChange={handelOnChange}
          rows="8"style={{backgroundColor: props.mode==='dark'? '#24183e': 'white', color:props.mode=== 'dark'?'white': 'black'}}
        ></textarea>
        <button className="btn btn-primary my-3" onClick={handelUpCLick}>
          upprecase
        </button>
        <button className="btn btn-primary mx-3 my-3" onClick={handelLowCLick}>
          lowercase
        </button>
        <button className="btn btn-primary " onClick={copyToClipboard}>
          Copy text
        </button>
        <button className="btn btn-primary mx-3" onClick={handleExtraSpaces}>
          Remove extra spaces
        </button>
        <button className="btn btn-primary " onClick={deleteAll}>
          delete all
        </button>
      </div>
      <div className="container" style={{ color:props.mode=== 'dark'?'white': 'black'}}>
        <h2>this is the sumery</h2>
        <p>
          {text.split(" ").filter((element)=>{return element.length !== 0}).length} word and {text.length} character
        </p>
        <p>{0.008 * text.split(" ").filter((element)=>{return element.length  !==0}).length} minites to read </p>
        <h2>privew</h2>
        <p>{text}</p>
      </div>
    </div>
  );
}
