// import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react' 
import Navbar from './component/Navbar';
import Textform from './component/Textform';
import Alert from './component/Alert';
import About from './component/About';
import ColorPalet from './component/ColorPalet';


import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

//  <Navbar title="harry" about="adarsh" />  
function App() {
 const [mode, setdarkMode] = useState('light');
 
 const [alert, setalert] = useState(null);
 
 const showAlert =(message, type)=>{
   setalert({
     msg:message,
     type:type
   })
   setTimeout(() => {
     setalert(null)
   }, 1500);
 }
 const removeBg =()=>{
document.body.classList.remove('bg-primary')
document.body.classList.remove('bg-light')
document.body.classList.remove('bg-dark')
document.body.classList.remove('bg-success')
document.body.classList.remove('bg-warning')
document.body.classList.remove('bg-danger')
 }
 const toggleMode =(cls)=>{
   removeBg()
   document.body.classList.add("bg-"+cls)
   console.log(mode);
   if (mode ==="dark" ) {
     setdarkMode('light')
    //  document.body.style.backgroundColor = 'white'
     showAlert('darkmode is colourmode' , 'success')
    //  document.title="Textutils -darkmode"
    //  setInterval(() => {
    //   document.title="Textutils is Amzising"
    //  }, 2000);
    //  setInterval(() => {
    //   document.title="Textutils install now"
    //  }, 1500);
   }else{
    setdarkMode('dark')
         //  document.body.style.backgroundColor = '#020d39'
     showAlert('you change the colour' , 'success')
     document.title="Textutils -lightmode"
   }
 }
//  return-------------------------------
  return (
    <>
    <Router>
      <Navbar
        logo="Dummy"
        home="HOME"
        aboutUs="ABOUTUS"
        contactUs="CONTACTUS"
        mode ={mode}
        toggleMode={toggleMode}
        ColorPalet={<ColorPalet toggleMode={toggleMode} />}
           />
           <Alert alert={alert}/>
   <div className="container">
        <Routes>
          <Route exact path="/About" element={<About mode={mode} />}></Route>
          <Route
            exact
            path="/"
            element={
              <Textform heading="Enter text" mode={mode} showAlert={showAlert} />
          
            }
          ></Route>
        </Routes>
      </div>
    </Router>
  </>
    );
}

export default App;
